import React from 'react';
import { NavLink } from 'react-router-dom';

const Common = (props)=>{
    return (
       <>
        <section id="header" className="d-flex align-item-center">
            <div className="container-fluid">
                <div className="row">
                    <div className = "col-10 mx-auto d-flex justify-content-center flex-collumn">
                        <div className="row">
                            <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1">
                                <h1>
                                {props.pageTitle} <span className="brand-name">TechHelp</span>
                                </h1>
                                <h4 className="my-3">
                                    We are the team of talented developers making websites
                                </h4>
                                <div className="mt-3">
                                    <NavLink to={props.btnLink} className="btn-get-started">{props.btnName}</NavLink>
                                </div>
                            </div>
                            <div className="col-lg-6 order-1 order-lg-2">
                                <img src={props.imgSrc} className="img-fluid animated" alt="hime img"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
       </>
    )
}

export default Common;